
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

@font-face {
    font-family: 'MagistralC';           /* название одно */
		src: url('../fonts/MagistralC.woff2') format('woff2'),
					url('../fonts/MagistralC.woff') format('woff'); /* разные файлы */
    font-weight: 400;              /* разная плотность */
      font-style: normal;
      font-display: swap;
  }
@font-face {
    font-family: 'MagistralC';           /* название одно */
