.form{
	&--col{
		display: flex;
	}

	&__input{
		border: none;
		background-color: #fff;
		height: vw(94);
		padding-left: vw(40);
		padding-right: vw(20);
		font-size: vw(20);
		margin-bottom: vw(20);
		width: calc(50% - 20px);
		&::placeholder{
			color: #777777;
		}

		&--soc{
			width: calc(50% - 25px);
			margin-right: vw(25);
		}
	}
	&__textarea{
		width: 100%;
		max-width: vw(700);
		padding-left: vw(40);
		padding-top: vw(42);
		padding-right: vw(20);
		padding-bottom: vw(40);
		border: none;
		background-color: #fff;
		min-height: vw(200);
		font-size: vw(20);
		&::placeholder{
			color: #777777;
			font-size: vw(20);
		}
	}

	&__buttons-wrap{
		display: flex;
		align-items: center;
		margin-top: vw(30);
	}

	&__service-list{
		display: flex;
		flex-wrap: wrap;
	}

	&__service{
		margin-left: vw(90);
		padding-top: vw(40);
	}

	&__service-title{
		color: #777777;
		font-size: vw(20);
		margin-bottom: vw(40);
	}

}
.form-wrapper{
	margin-top: vw(70);
}
.form-btn{
	width: vw(405);
}
.form-col{
	width: vw(700);
	flex-shrink: 0;
}
.inputs-row{
	display: flex;
	justify-content: space-between;
	max-width: vw(700);
	
}
.file-upload-label{
	margin-left: vw(45);
	cursor: pointer;
	&:hover{
		.file-upload-text{
			text-decoration: none;
		}
	}
}

.file-upload-icon{
	vertical-align: middle;
	margin-right: vw(10);
}
.file-upload-text{
	vertical-align: middle;
	color: #919191;
	font-size: vw(19);
	text-decoration: underline;
}


.label-checkbox{
	margin-bottom: vw(45);
	display: inline-flex;
	cursor: pointer;
	width: 50%;

	.custom-checkbox{
		width: vw(25);
		height: vw(25);
		border: 1px solid #838583;
		margin-right: vw(12);
		position: relative;
		&:after{
			content: '';
			width: 65%;
			height: 65%;
			background-color: $accent-color;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			opacity: 0;
			transition: opacity .2 ease;
		}
	}

	.checkbox-text{
		font-size: vw(22);
	}

	input[type="checkbox"]:checked ~ .custom-checkbox:after{
		opacity: 1;
	}
}


.form-row{

	display: flex;
	flex-wrap: wrap;

	&--top{
		input{
			width: calc(93% / 4);
			&:not(:last-child){
				margin-right: vw(25);
			}
		}
	}
}

.form-dd_wpar{
	width: 50%;
}

.input-light{
	background-color: transparent;
	border: none;
	border-bottom: 1px solid rgba(#000000, 0.2);
	
	width: 70%;
	padding: vw(10) vw(10);
	&::placeholder{
		color: #A7A7A7;
		font-size: vw(21);
	}
}