.documents{

	&__wrap{
		display: flex;
		justify-content: space-between;
	}
	&__title{
		margin: 0;
		line-height: 1.3;
		position: relative;
		z-index: 2;
	}
	&__label{
		font-size: vw(217);
		font-family: $second-font;
		text-transform: uppercase;
		color: #F7F7F7;
		font-weight: 700;
		transform: translateY(vw(-120));
		position: relative;
		z-index: 1;
	}

	&__list{
		display: flex;
		margin-top: vw(15);
		flex-wrap: wrap;
		max-width: vw(820);
	}

	&__item{
		width: vw(325);
		position: relative;
		min-height: 260px;
		margin-bottom: 80px;
		&::after{
			content: '';
			display: block;
			width: 100%;
			height: 4px;
			background-color: #F4F4F4;
			position: absolute;
			bottom: 0;
		}
		&:nth-child(odd){
			margin-right: vw(150);
		}
		&:hover{
			.documents__icon{
				transform: translateY(-5px);
			}
			.documents__name{
				border-bottom-color: #000;
			}
		}
	}
	&__icon{
		margin-bottom: vw(30);
		width: vw(100);
		transition: all .3s ease;
	}
	&__name{
		color: #111415;
		font-size: vw(24);
		line-height: 1.2;
		margin-bottom: vw(25);
		border-bottom: 1px solid transparent;
	}
	&__size{
		font-size: vw(24);
		color: #959595;
		font-weight: 300;
		display: block;
	}
}