.breadcrumbs{
	margin-top: vw(30);
	&__list{
		display: flex;
		margin: 0;
		padding: 0;
	}

	&__item{
		color: #B7B7B7;
		margin-right: vw(30);
		font-size: vw(18);
		a{
			color: inherit;
			text-decoration: underline;
		}

		&:last-child{
			color: #838383;
			a{
				text-decoration: none;
			}
		}
	}
}