.working{
	padding-bottom: vw(140);
	&__title{
		margin-bottom: vw(70);
	}
	&__list{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
}

.working-item{
	width: calc(100% / 5 - 70px);
	min-width: 140px;

	&:nth-child(even){
		padding-top: vw(50);
		border-top: 4px solid #E2E2E2;
	}
		&:nth-child(odd){
		padding-bottom: vw(50);
		border-bottom: 4px solid #E2E2E2;
	}
	&:first-child{
		border-bottom-color: $accent-color;
	}
	&:last-child{
		border-bottom-color: $accent-color;
	}

	&__wrap{
		background: #F7F7F7;
		padding-top: vw(70);
		padding-bottom: vw(50);
		text-align: center;
	}

	&__icon{
		margin-bottom: vw(33);

		img{
			max-width: vw(106);
		}
	}

	&__number{
		color: #939393;
		font-size: vw(42);
		font-family: $second-font;
		font-weight: 700;
		margin-bottom: vw(12);
	}
	&__text{
		font-size: vw(21);
		line-height: vw(25);
	}
}