.header{
	padding-top: vw(42);
	padding-bottom: vw(27);
	border-bottom: 1px solid #DDDDDD;
	&__inner{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo{
		max-width: vw(245);
	}

	&__phone-icon{
		vertical-align: middle;
		margin-right: vw(16);
	}
	&__phone-link{
		vertical-align: middle;
		font-size: vw(22);
		font-weight: bold;
		color: #333333;
		border-bottom: 1px solid #000;
	}
	&__nav{
		margin-bottom: vw(17);
		margin-left: vw(60);
	}
	&__phone{
		margin-bottom: vw(17);
	}

	&__phone-link{
		
		&:hover{
			color: $accent-color;
			border-bottom-color:transparent;
		}
	}

}


.nav{
	&__list{
		display: flex;
		margin: 0;
	}

	&__item{
		&:not(:last-child){
			margin-right: vw(50);
		}
	}
	&__link{
		font-size: vw(22);
		color: #333333;
		position: relative;
		&:after{
			content: '';
			width: 0;
			height: 2.5px;
			background-color: $accent-color;
			display: block;
			transition: all .3s ease-in-out;
			position: absolute;
			left: 0;
		}
		&:hover{
			&:after{
				width: 100%;
			}
		}
		&--active{
			&:after{
				width: 100%;
			}
		}
	}
}

.burger {
	width: 30px;
	height: 17px;
	-webkit-box-pack: justify;
	justify-content: space-between;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	flex-direction: column;
	-webkit-box-align: end;
	align-items: flex-end;
	position: relative;
	z-index: 60;
	display: none;	
}

.burger__item {
	width: 100%;
	height: 3px;
	background-color: #169877;
	-webkit-transition: all .3s ease;
	transition: all .3s ease
}

.burger__item--three {
	opacity: 1
}

.burger--active .burger__item--one {
	-webkit-transform: rotate(45deg) translateY(5px);
	transform: rotate(45deg) translateY(5px)
}

.burger--active .burger__item--two {
	-webkit-transform: rotate(-45deg) translateY(-5px);
	transform: rotate(-45deg) translateY(-5px)
}

.burger--active .burger__item--three {
	opacity: 0
}