.about{
	background: #F8F8F8;
	padding-top: vw(20);

	&__inner{
		display: flex;
	}
	&__info{
		flex-shrink: 0;
		display: flex;
	}
	&__man{
		margin-left: vw(-93);
		position: relative;
		z-index: 5;
		max-width: vw(600);
	}
	&__body{
		margin-left: vw(-135);
	}

	&__title{
		font-family: $second-font;
		font-size: vw(44);
		font-weight: 700;
		line-height: 1.3;
		margin-bottom: vw(35);
	}

	&__body-text{
		background: #FFFFFF;
		border: 1px solid #E4E4E4;
		max-width: vw(625);
		padding-top: vw(45);
		padding-right: vw(60);
		padding-bottom: vw(40);
		padding-left: vw(80);
		font-size: vw(24);
		margin-bottom: vw(97);
	}

	&__licenses{
		padding-top: vw(180);
		padding-left: vw(66);
	}
}

.man-caption{
	margin-left: vw(185);
	&__label{
		font-size: vw(22);
		color: #fff;
		background-color: $accent-color;
		padding: vw(10) vw(25);
		display: inline-block;
		flex-shrink: 0;
		flex-grow: 1;
	}
	&__name{
		color: #8A8A8A;
		background-color: #fff;
		font-size: vw(18);
		padding: vw(10) vw(25);
		display: inline-block;
	}
}

.about-licenses{
	&__title{
		font-size: vw(24);
		font-weight: 400;
		margin-bottom: vw(20);
	}

	&__subtitle{
		color: #ABABAB;
		font-size: vw(24);
		line-height: vw(24);
		margin-bottom: vw(35);
	}
	&__list{
		display: flex;
	}
}

.license{
	text-align: center;
	
	&__pic{
		background-color: #fff;
		padding: vw(17) vw(13);
		display: inline-block;
		margin-bottom: vw(14);
		position: relative;
			&::after{
				content: '';
				width: calc(100% - 26px);
				height: calc(100% - 40px);		
				display: block;
				position: absolute;
				top: vw(17);
				left: vw(13);
				background: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25));
				z-index: 2;
				opacity: 0;
				transition: all .3s ease;
			}
		

		img{
			// max-width: vw(155);
		
		}
		&:hover{
			&:after{
				opacity: 1;
			}
			.license__plus{
				opacity: 1;
			}
		}
	}
	&__name{
		color: #7C7C7C;
		font-size: vw(16);
	}
	&__plus{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 5;
		opacity: 0;
		transition: all .3s ease;
	}
}