.hero{
	padding-bottom: vw(127);
	position: relative;
		&:before{
			content: '';
			width: 54.5%;
			height: vw(250);			
			background: #F9F9F9;
			position: absolute;
			bottom: 0px;
			right: 0;
			z-index: 1;			
		}
		&:after{
			content: '';
			width: vw(530);
			height: vw(90);
			background-image: url(../img/dots-bg.png);
			background-repeat: no-repeat;
			position: absolute;
			left: 0;
			bottom: 0;
		}

	&__content{
		flex: 0 0 100%;
		position: relative;
		z-index: 5;
	}

	&__image{
		margin-top: vw(35);
		position: relative;
		z-index: 5;

		img{
			position: relative;
			z-index: 5;
		}

	
		
	}

	&__body{
		padding-top: vw(113);
		width: 50%;
	}

	&__title{
		line-height: 1.2;
		margin-bottom: vw(40);

		.color-green{
			border-bottom: 4px solid $accent-color;
		}
	}

	&__subtitle{
		font-size: vw(30);
		max-width: vw(580);
		line-height: 1.3;
		font-weight: 400;
		letter-spacing: 1.2px;
		margin-bottom: vw(50);
	}
}

@media (min-width: 768px) {
	.hero{
		display: flex;

		&__image{
			flex: 0 0 50%;
			transform: translateX(-100%);
		}

	}
}

