
.popup{
	margin: 0 auto;
	max-width: vw(1300);
	background-color: #fff;
	box-shadow: 0px 14px 31px rgba(114, 114, 114, 0.25);
	padding-top: vw(80);
	padding-bottom: vw(90);
	text-align: center;
	font-family: $second-font;
	position: relative;

	&__title{
		font-size: vw(49);
		font-weight: bold;
		margin-bottom: 15px;
	}

	&__subtitle{
		font-size: vw(30);
		margin-bottom: vw(60);
	}

	&__form-input{
		height: vw(55);
		padding-left: vw(40);
		padding-right: vw(20);
		color: #777777;
		font-size: vw(20);
		background: #F8F8F8;
		border: 0;
		margin-right: 10px;
	}
	&__form-btn{
		height: vw(55);
		background: #169877;
		border: 0;
		font-size: vw(21);
		font-weight: bold;
		color: #fff;
		padding-left: vw(60);
		padding-right: vw(60);
		cursor: pointer;
	}

	&__footer{		
		color: #BEBEBE;
		font-size: vw(18);
		a{
			color: #7E7E7E;
			text-decoration: underline;
		}
	}
}

.mfp-close-btn-in .mfp-close {
    color: #fff;
    right: -80px;
}
.mfp-zoom-in .popup {
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8); }

.mfp-zoom-in.mfp-bg {
  opacity: 0;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out; }

.mfp-zoom-in.mfp-ready .popup {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1); }

.mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8; }

.mfp-zoom-in.mfp-removing .popup {
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  opacity: 0; }

.mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0; }
	
	.mfp-close-btn-in .mfp-close {
    color: #E2E2E2;
		right: 0;
		font-size: 40px;
		opacity: 0.8;
}

.mfp-bg{
	background: #fff;
}

/* padding-bottom and top for image */
.mfp-no-margins img.mfp-img {
	padding: 0;
}
/* position of shadow behind the image */
.mfp-no-margins .mfp-figure:after {
	top: 0;
	bottom: 0;
}
/* padding for main container */
.mfp-no-margins .mfp-container {
	padding: 0;
}


