.footer{
	background-color: #333333;
	color: #fff;
	a{
		color: inherit;
	}

	&__top{
		padding-top: vw(60);
		padding-bottom: vw(56);
	}
	
	&__bottom{
		background: #1C1C1C;
		color: #9C9C9C;
		padding-top: vw(30);
		padding-bottom: vw(40);
	}

	&__inner{
		display: flex;
		justify-content: space-between;
	}
	&__logo-link{
		margin-bottom: vw(20);
		display: inline-block;
		width: vw(245);
	}

	&__contact{
		a, span{
			display: block;
			text-align: right;
		}
	}
	&__phone{
		font-weight: 700;
		font-size: vw(22);
	}

	&__contact-caption{
		color: #C4C4C4;
		font-size: vw(18);
	}
	&__mail{
		font-size: vw(20);
		margin-top: vw(25);
	}

	&__policy{
		a{
			text-decoration: underline;
			font-size: vw(20);
		
			&:hover{
				text-decoration: none;
			}
		}
	}
	&__logo{
		margin-top: vw(30);
	}
	&__contact{
		margin-top: vw(12);
		text-align: right;
	}
	&__developer{
		margin-top: 50px;
		    display: inline-flex;
		flex-direction: column;
		span{
			text-align: left;
		}
	}

	&__developer-link{
		margin-bottom: 10px;
	}

	&__developer-caption{
		color: #C4C4C4;
		font-size: 11px;
	}
}

.chat{
	&__head{
		display: block;
		margin-bottom: vw(16);
		color: #C1C1C1;
		font-size: vw(18);	
	}
	&__btn{
		display: flex;
		align-items: center;
		justify-content: center;
		height: vw(63);
		width: vw(210);
		font-size: vw(22);
		background: rgba(#B0B0B0, 0.07);
		img{
			margin-right: vw(5);
		}
		span{
			color: #2CC54E;
		}
	}
}

.nav-footer{
	display: flex;

	&__list{
		margin-bottom: 0;

		&:not(:last-child){
			margin-right: vw(130);
		}
	}
	&__item{
		margin-bottom: vw(23);
		a{
		
			&:hover{
				opacity: 0.7;
			}
		}
	}
}