.our-mission{
	padding-top: vw(70);
	padding-bottom: vw(100);

	&__head{
		max-width: vw(730);
		margin-bottom: vw(50);
	}

	&__title{
		margin-top: 0px;
		line-height: 1.3;
		margin-bottom: vw(20);
	}
}

.benefits{
	display: flex;
	justify-content: space-between;
}

.benefit{
	border-right: 2px solid rgba($color: #A6A6A6, $alpha: 0.2);
	// width: calc(100% / 4);
	// width: 350px;
	padding-top: vw(40);
	padding-bottom: vw(50);
	
	&:nth-child(1){
		padding-right: vw(95);
	}
	&:nth-child(2){
		padding-left: vw(120);
		padding-right: vw(115);
	}
	&:nth-child(3){
		padding-left: vw(95);
		padding-right: vw(140);
	}
	&:last-child{
		border-right: 0;
		padding-left: vw(120);
	}
	&__number{
		font-family: $second-font;
		font-size: vw(66);
		font-weight: 700;

		span{
			opacity: 0;
		}
	}

	&__decr{
		font-size: vw(24);
	}
}