

.hero-vacancies{
	padding-bottom: 0;
	&:before{
		display: none;
	}

	&:after{
		width: vw(430);
		height: (175);
		left: 70%;
		bottom: 67%;
	}

	.hero__body{
		padding-top: vw(65);
	}
	.hero__title{
		margin-bottom: 0;
	}
	.hero__subtitle {
		max-width: vw(800);
	}
	.hero__image{
		margin-top: vw(62);
		width: vw(1024);
	}
	.hero__image img{
		// max-width: none;
		vertical-align: bottom;
	}
}


.vacancies-about{
	position: relative;
	padding-top: vw(50);
	padding-bottom: vw(125);

	&:before{
		content: '';
		width: 100%;
		height: vw(480);
		background: #F8F8F8;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
	}
	&:after{
			content: '';
			width: vw(375);
			height: vw(125);
			background-image: url(../img/dots-bg.png);
			background-repeat: no-repeat;
			position: absolute;
			left: 26%;
			bottom: 0;
		}

	&__title{
		margin-top: 0;
		margin-bottom: 0;
	}

	&__subtitle{
		max-width: vw(510);
		font-size: vw(24);
		line-height: vw(30);
		margin-top: vw(15);
		margin-bottom: vw(65);
	}

	&__inner{
		display: flex;
	}
	&__info{
		margin-right: vw(190);
		flex-shrink: 0;
	}

	&__benefit{
		display: flex;
		flex-wrap: wrap;
	}
	&__benefit-item{
		width: 50%;

		&:nth-child(1){
			margin-bottom: vw(140);
		}
		&:nth-child(2){
			margin-bottom: vw(140);
		}
	}

	&__benefit-icon{
		margin-bottom: vw(30);
		width: vw(100);
	}

	&__benefit-title{
		font-weight: 700;
		font-size: vw(25);
		margin-bottom: vw(15);
	}

	&__benefit-text{
		font-size: vw(24);
		line-height: vw(28);
	}

	&__slider{
		max-width: vw(700);
	}
}