.contact-page{
	position: relative;
	padding-bottom: vw(50);
	margin-top: vw(-30);

	.breadcrumbs{
		padding-top: vw(30);
	}

	&__map{
		width: vw(890);
		height: 100%;
		position: absolute;
		right: 0;
		top: 0x;

		#map{
			width: 100%;
			height: 100%;
		}
		.ymaps-layers-pane {
    	-webkit-filter: grayscale(100%);
		}
	}

	&__columns{
		display: flex;
		// width: 50%;
		justify-content: space-between;
		margin-right: vw(890);
	}

	&__col{
		// width: 25%;
	}

	&__item{
		margin-bottom: vw(60);
		height: vw(150);
	}

	&__label{
		color: #BFBFBF;
		font-size: vw(23);
		margin-bottom: vw(20);
	}
	&__phone{
		color: #333333;
		font-weight: bold;
		font-size: vw(32);
	}
	&__info{
		font-size: vw(28);

		&--color-grey{
			color: #838383;
		}
		p{
			margin-top: 0;
		}
	}
	&__chat{
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba(#2CC54E, 0.07);
		width: 211px;
		height: 63px;
		color: #2CC54E;
		font-size: 22px;
		img{
			margin-right: 5px;
		}
	}
}