.s-services__control{
	left: 0;

	.slider-control-right{
		width: auto;	
		height: auto;
		&:hover{
			background-color: transparent;
			svg{
				fill:#ABABAB;
			}
			.slider-control-text{
				color: #ABABAB;
			}
		}

		svg{
			fill:#169877;
		}
	}
}

.slider-controls{
	display: flex;
	align-items: center;
	    position: relative;
    top: vw(-15);
		z-index: 50;
		&--main{
			left: calc(45.5% + 150px)!important;
		}

}

.slider-controls--main{
	.slider-control-left,
	.slider-control-right{
			width: vw(87);
  height: vw(80);
	}
}

.slider-control-left,
.slider-control-right{
	width: vw(87);
  height: vw(80);
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all .2s ease;
	background-color: transparent;
	outline: none;

	&:hover{
		background: rgba($color: #169877, $alpha: 0.11);
		svg{
			fill:#169877;
		}
	}

	svg{
		fill:#ABABAB;
	}
}

.swiper-pagination-fraction{
	width: auto;
	bottom: 0;
}
.slider-pagination-number{
	color: #ABABAB;
	font-size: vw(35);

	.swiper-pagination-current{
		font-size: vw(51);
		line-height: 0;
	}
}
.swiper-slide,
.swiper-slide.swiper-slide-next,
.swiper-slide.swiper-slide-prev{
	.s-services__pic{
	position: relative;
	&:after{
		content: '';
		width: 100%;
		height: 100%;
		background-color: #fff;
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
		transition: all .9s cubic-bezier(0.215, 0.61, 0.355, 1) 0.7s;
	}
}

.s-services__buttons{
	opacity: 0;
}

}

.swiper-slide.swiper-slide-active{

	.s-services__pic{

		&:after{
			width: 0;
			// transition: all 0.4s 0.6s ease-in-out;
		}
	}
	.s-services__text,
	.s-services__title,
	.s-services__buttons{
	// 	transform: translate3d(0, 0, 0);
  // opacity: 1;
	}

}

.swiper-slide .s-services__title {

	opacity: 0;
	transform: translatex(0);
  transition: transform 0.8s 0.6s, opacity 0.8s 0.6s;
}
.swiper-slide .s-services__buttons {
 
	opacity: 0;
	transform: translatex(0);
  transition: transform 0.8s 0.8s, opacity 0.8s 0.8s;
}
.swiper-slide .s-services__text{
 
