html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

@function vw($value, $base: 1920) {
	@return $value / $base * 100vw;
}

@function vmin($value, $base: 320) {
	@return $value / $base * 100vmin;
}

body {
  display: flex;      
  flex-direction: column;  
  margin: 0;
  min-height: 100%;
  word-break: break-word;
  font-family: $main-font;
  font-size: vw(18);
  line-height: 1.4;
  text-align: left;
	color: #333333;
	background: #fff;
	overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}
	html{
		overflow-x: hidden;
	}
img {
  max-width: 100%;
}

a {
	text-decoration: none;
	transition: all .3s ease-in;
}

ul {
  list-style: none;
  padding: 0;
}
textarea {
  overflow: auto;   
  resize: vertical; 
}

.page {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}
.hidden-lg{
	display: none;
}

.for-mobile {
  display: none!important;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.btn--reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
	max-width: vw(1630);
  padding: 0 vw(15);
  margin: 0 auto;
	position: relative;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2{
	font-size: vw(59);
	font-family: $second-font;
}

.color-green{
	color: $accent-color;
}

.site-subtitle{
	font-size: vw(30);
}