
.s-services{
	position: relative;
	padding-bottom: vw(30);
	&__wrap{
		position: relative;
		width: 100%;
	}
	&__label{
		position: absolute;
		left: 53.5%;
		top: 108px;
		background: #F3F3F3;
		color: #ABABAB;
		font-size: vw(24);
		padding: vw(20) vw(35);
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 20;		

		&--dark{
			background: #333333;
			color: #fff;
		}

	}
	&__control{
		position: absolute;
    top: 91%;
		    left: calc(45% + 150px);
	}

	&__slide{
		display: flex;
		
	}
	&__content{
		padding-top: vw(275);
	}
	&__pic{
		width: 45.5%;
		margin-right: vw(150);
		
	}

	&__title{
		font-size: vw(48);
		font-family: $second-font;
		font-weight: 700;
		margin-bottom: vw(88);
		//  transition: all .7s cubic-bezier(0.215, 0.61, 0.355, 1) 0.9s;
		// 		transform: translate3d(-20%, 0, 0);
		// 		opacity: 0;
	}
	&__buttons{
		// transition: all .9s cubic-bezier(0.215, 0.61, 0.355, 1) 1.2s;
		// 		transform: translate3d(-20%, 0, 0);
		// 		opacity: 0;
	}

	&__text{
		transition: all .8s cubic-bezier(0.215, 0.61, 0.355, 1) 1.1s;
				transform: translate3d(-20%, 0, 0);
				opacity: 0;
	}
}


.services-page__info{
	display: flex;
	justify-content: flex-end;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 20;

	.s-services__label{
		position: relative;
		left: 0;
		top: 0;
		display: inline-flex;
		height: vw(80);
	}

}

.services-nav{
	background-color: #fff;
	padding-top: vw(20);
	padding-bottom: vw(20);
	padding-left: vw(45);
	padding-right: vw(50);
	max-width: vw(390);

	&__item{
		&:not(:last-child){
			margin-bottom: vw(30);
		}
		a{
			color: #8F8F8F;
			font-size: vw(22);
			border-bottom: 1px solid transparent;

			&:hover{
				color: #333;
				border-bottom-color: #333;
			}
		}

		&.is-active{
			a{
				color: #333;
				border-bottom-color: #333;
			}
		}
	}
}

.services-page{
	padding-bottom: vw(130);
}

.services-page__slider{

	.s-services__slide{
		padding-top: vw(40);
	}

	.s-services__content{
		padding-top: vw(55);
		max-width: vw(640);
		margin-right: vw(85);
	}
	.s-services__title{
		margin-bottom: vw(40);
	}
}

.s-services__text{
	color: #333333;
	font-size: vw(25);
	line-height: vw(34);
	margin-bottom: vw(50);
}


.services-page__control{
	display: flex;
	align-items: center;
	padding-left: vw(25);
	padding-top: vw(20);
	padding-bottom: vw(20);
	margin-left: vw(25);
	border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
}
.slider-control-text{
	color: #169877;
	font-size: vw(24);
	padding-right: vw(15);
}

.services-page__slider .s-services__control{
	left: 0;
}


.what-offer{
	padding-bottom: vw(80);
	position: relative;
	.container{
		position: relative;
	}

	&__dots{
		position: absolute;
		right: -125px;
		top: 240px;
		width: vw(400);
	}

	&__icon{
		position: absolute;
		top: 0;
		left: 0;
		background: #F8F8F8;
		width: vw(820);
		height: vw(950);
		img{
			position: absolute;
			bottom: 0;
			left: 0;
			width: vw(495);
		}

	}

	&__content{
		padding-left: vw(520);
		position: relative;
		z-index: 10;
		padding-top: vw(70);
	}

	&__title{
		color: #169877;
		font-weight: 500;
		font-size: vw(49);
	}

	&__list{
		list-style-type: none;
		counter-reset:  item;
		padding-left: 0;
		li{
			padding-top: vw(40);
			padding-bottom: vw(40);
			border-bottom: 1px solid #D3D3D3;
			font-size: vw(24);
			position: relative;
			padding-left: vw(70);
			&:before{
				content: '0' counter(item) ' ';
				counter-increment: item;
				color: #939393;
				font-size: vw(30);
				font-weight: 300;
				font-family: 'MagistralC' ,sans-serif; 
				padding-right: 14px;
				position: absolute;
				top: vw(37);
				left: 0;
			}

			&:last-child{
				border-bottom: none;
			}
		}
	}
}